import noData from '@/components/noData'
import back from '@/components/back'
export default {
	components: {
		noData,
		back
	},
	data() {
		return {
			info:null,
			detailsList:null,
			temp:false,
			title:""
		}
	},
	beforeDestroy() {
		window.onscroll = null
	},
	created() {
		this.pull()
	},
	mounted() {
		
	},
	methods: {
		pull(h) {
			this.$post('/PlatFormAPI/SpecialistHosGroup/GetGoodsDetails', {
				Id: this.$route.query.id,
				SpecialistGoodsId: this.$route.query.specialistId,
				DrId:0,
			}).then(o => {
				this.info = o
				if(o && o.DescPic){
					this.detailsList = o.DescPic.split(";")
				}
				this.temp = true
				this.title = (o && o.GoodsName) ? o.GoodsName : "商品详情"
			})

		},
	}
}



